<template>
    <div>
        <ul class="plugin-list">
            <li>
                <h3>Name：</h3>
                <div class="text">Calendar</div>
            </li>
            <li>
                <h3>Introduction：</h3>
                <div class="text">移动端的日期插件</div>
            </li>
            <li>
                <h3>Demo：</h3>
                <div id="demoDate">
                    <!--日历展示-->
                </div>
                <div class="text">
                    您选择的日期为：<input type="text" readonly="readonly" v-model="nowVal">
                </div>
            </li>
            <li>
                <h3>Function：</h3>
<pre class="JS">
// 日历
var calendar = new Calendar(
    '#demoDate',//id

    //参数
    {
        // 最小限制日期（参数可选）
        minDate: '2017-02-11',

        // 最大限制日期（参数可选）
        maxDate: '2017-05-11',

        // 回调（参数可选）
        callBack: function (nowVal) {
            self.nowVal = nowVal;
        }
    }
);
</pre>
            </li>
            <li>
                <h3>Usage：</h3>
                <div class="text">
                    引入文件：</br>
                    'calendar/calendar.css'</br>
                    'calendar/calendar.js'
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
    import Calendar from '../../lib/calendar/calendar'

	export default {
		name: 'calendar',
        data(){
            return {
                nowVal: ''
            }
        },
        mounted(){
            var self = this;

            // 日历
            var calendar = new Calendar('#demoDate', {
                // 最小限制日期（参数可选）
                minDate: '2017-02-11',

                // 最大限制日期（参数可选）
                maxDate: '2017-05-11',

                // 回调（参数可选）
                callBack: function (nowVal) {
                    self.nowVal = nowVal;
                }
            });
        },
	}
</script>

<style scoped>
    @import "../../lib/calendar/calendar.css";
</style>


